
jQuery.fn.showOn = function(option) {

  $(this).each(function() {

    $(this)
      .focus(function() {
        var classname = option.bind[$(this).attr('id')];
        $(option.target).addClass(classname);
      })
      .blur(function() {
        var classname = option.bind[$(this).attr('id')];
        $(option.target).removeClass(classname);
      })
  })

}

